export default {
	props: {
		headerShow: {
			type: Boolean,
			default: true
		},
		inputDisabled: {
			type: Boolean,
			default: false
		},
		placeholder: {
			type: String,
			default: '请输入关键字'
		},
		icon: {
			type: String,
			default: null
		},
		title: {
			type: String,
			default: null
		},
	},
	data() {
		return {
			afterHeaderOpacity: 1,//不透明度
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			nVueTitle: null,
			searchValue: this.placeholder
		}
	},
	methods: {
		handleSearchValueChange(e) {
			this.searchValue = e.detail.value;
		},
		discard() {
		},
		link() {
			this.$emit('link')
		},
		async toSearch() {
			await this.$emit('search', {
				searchValue: this.searchValue
			})
		}
	}
}
